.dangerStrip {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  z-index: 1000;
  overflow: hidden;
  background-image: url('../../../../assets/pre/Border=Striped2.png');
  background-repeat: repeat-x;
  background-size: auto 100%;
  border-bottom: 2px solid #ff0000;
  box-shadow: 0 2px 10px rgba(255, 0, 0, 0.5);
  animation: stripePulse 1s infinite ease-in-out;
}

.content {
  position: relative;
  width: 100%;
  height: 100%;
  background: rgba(255, 0, 0, 0.8);
  display: flex;
  align-items: center;
  overflow: hidden;
}

.scrollingText {
  display: flex;
  align-items: center;
  white-space: nowrap;
  font-weight: bold;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  font-size: 14px;
  gap: 40px;
  animation: scrollLeft 15s linear infinite;
  min-width: 200%;
}

.message {
  color: #ffff00;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.multiplier {
  color: #ff6666;
  font-weight: bold;
  background: rgba(0, 0, 0, 0.5);
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #ff0000;
}

.timeRemaining {
  color: #ffffff;
  font-family: 'Courier New', monospace;
  background: rgba(0, 0, 0, 0.6);
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #ffffff;
}

@keyframes scrollLeft {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes stripePulse {
  0%, 100% {
    box-shadow: 0 2px 10px rgba(255, 0, 0, 0.5);
  }
  50% {
    box-shadow: 0 2px 20px rgba(255, 0, 0, 0.8), 0 0 10px rgba(255, 0, 0, 0.6);
  }
}

/* Адаптивный дизайн */
@media (max-width: 768px) {
  .dangerStrip {
    height: 40px;
  }
  
  .scrollingText {
    font-size: 12px;
    gap: 30px;
  }
  
  .message {
    font-size: 14px;
  }
} 