.restartCoreContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background: rgba(10, 20, 30, 0.95);
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 200, 255, 0.4);
  color: #e0f0ff;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

.gameHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
  
  h3 {
    margin: 0;
    color: #00ccff;
    font-size: 24px;
  }
}

.timer {
  background: rgba(0, 0, 0, 0.3);
  padding: 8px 12px;
  border-radius: 4px;
  font-family: monospace;
  font-size: 18px;
  
  span {
    font-weight: bold;
    color: #00ff99;
    
    &.timerWarning {
      color: #ff3333;
      animation: pulse 1s infinite;
    }
  }
}

.message {
  background: rgba(0, 0, 0, 0.5);
  padding: 10px 15px;
  border-radius: 4px;
  margin-bottom: 15px;
  text-align: center;
  font-weight: bold;
  color: #ffcc00;
  animation: fadeIn 0.3s ease-in-out;
}

.panelsGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 10px;
  width: 100%;
  aspect-ratio: 1 / 1;
  margin-bottom: 20px;
}

.panel {
  position: relative;
  background: rgba(30, 60, 90, 0.6);
  border: 2px solid rgba(0, 150, 200, 0.5);
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &:hover:not(.active):not(.disabled) {
    background: rgba(40, 80, 120, 0.8);
    box-shadow: 0 0 15px rgba(0, 200, 255, 0.4);
    transform: scale(1.03);
  }
  
  &.active {
    background: rgba(0, 200, 100, 0.6);
    border-color: rgba(0, 255, 100, 0.9);
    box-shadow: 0 0 15px rgba(0, 255, 100, 0.5);
  }
  
  &.disabled {
    background: rgba(150, 30, 30, 0.6);
    border-color: rgba(200, 0, 0, 0.5);
    cursor: not-allowed;
    opacity: 0.7;
  }
  
  &.falling {
    animation: fall 0.5s ease-in forwards;
    background: rgba(200, 50, 50, 0.8);
    border-color: rgba(255, 0, 0, 0.8);
    cursor: not-allowed;
  }
  
  &.start {
    background: rgba(0, 150, 255, 0.6);
    border-color: rgba(0, 200, 255, 0.9);
  }
  
  &.finish {
    background: rgba(255, 100, 0, 0.4);
    border-color: rgba(255, 150, 0, 0.8);
  }
}

.startMarker, .finishMarker {
  position: absolute;
  font-size: 12px;
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 3px;
}

.startMarker {
  top: 5px;
  left: 5px;
  background: rgba(0, 100, 255, 0.8);
  color: white;
}

.finishMarker {
  top: 5px;
  right: 5px;
  background: rgba(255, 100, 0, 0.8);
  color: white;
}

.gameInstructions {
  text-align: center;
  margin-bottom: 20px;
  font-size: 14px;
  color: #aaccff;
  max-width: 400px;
  line-height: 1.4;
}

.gameOverMessage {
  margin-top: 20px;
  padding: 15px 20px;
  border-radius: 5px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  animation: fadeIn 0.5s ease-in-out;
  
  &.success {
    background: rgba(0, 150, 50, 0.3);
    color: #00ff99;
    border: 2px solid rgba(0, 200, 100, 0.5);
  }
  
  &.failure {
    background: rgba(150, 30, 30, 0.3);
    color: #ff5555;
    border: 2px solid rgba(200, 50, 50, 0.5);
  }
}

.progress {
  text-align: center;
  margin-bottom: 15px;
  font-size: 16px;
  color: #00ccff;
  font-weight: bold;
  background: rgba(0, 0, 0, 0.3);
  padding: 8px 12px;
  border-radius: 4px;
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fall {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(20px);
  }
} 