.popupBackdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.popupContent {
  background: linear-gradient(135deg, var(--alabaster) 0%, var(--white-smoke) 100%);
  border: 3px solid var(--tomato);
  border-radius: 15px;
  padding: 30px;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease-out;
  text-align: center;
}

.header {
  margin-bottom: 20px;
  
  h2 {
    color: var(--cornell-red);
    font-size: 24px;
    font-weight: bold;
    margin: 0;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  }
}

.message {
  margin-bottom: 25px;
  
  p {
    color: var(--eerie-black);
    font-size: 18px;
    line-height: 1.5;
    margin: 0;
    
    strong {
      color: var(--tomato);
      font-weight: bold;
    }
  }
}

.okButton {
  background: linear-gradient(135deg, var(--tomato) 0%, var(--cinnabar) 100%);
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 30px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 96, 72, 0.3);
  
  &:hover {
    background: linear-gradient(135deg, var(--cinnabar) 0%, var(--cornell-red) 100%);
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(255, 96, 72, 0.4);
  }
  
  &:active {
    transform: translateY(0);
    box-shadow: 0 2px 10px rgba(255, 96, 72, 0.3);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
} 