.projects {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  color: var(--alabaster);
  text-shadow: 1px 1px 0 var(--eerie-black);

}

.project {
  display: flex;
  transition: all 0.3s ease;
  overflow: hidden;
  position: relative;

  &:hover {    

    
    .description {
      opacity: 1;
    }
  }
}

.content {
  flex: 1;
}

.header {
  margin-bottom: 4px;
  z-index: 1;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-weight: normal;
  margin: 0;
  display: inline;
  padding-left: 0.5em;
}

.progressText {
  font-size: 0.9em;
}

.description {
  font-size: 0.6em;
  opacity: 0;
  transition: all 0.3s ease;
  z-index: 1;
  position: relative;
  top: -0.5em;
  text-align: left;
  padding-left: 0.8em;
}

.progressContainer {
  width: 100%;
  overflow: hidden;
}

.progressBar {
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 0%;
  transition: width 0.5s ease, height 0.3s ease, background-color 0.3s ease;
}

.icon {
  font-size: 16px;
  transition: font-size 0.3s ease;
  z-index: 1;
  position: relative;
  text-shadow: none;
}

.success {
  .progressBar {
    background-color: #2ecc71;
  }
}

.failed {
  .progressBar {
    background-color: var(--cinnabar);
  }
  
  .title, .progressText, .description {
    text-decoration: line-through;
  }
}

.completed {
  animation: fadeOut 5s forwards;
  animation-delay: 5s;
}

.failed {
  animation: fadeOut 5s forwards;
  animation-delay: 10s;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    height: 0;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
}

.projectsContainer {
  padding: 1rem;
  border-radius: 8px;
  overflow-y: auto;
  max-height: 100%;
  .title {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: #333;
  }
  
  .projectsList {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
}

.project {
  display: flex;
  align-items: center;
  
  .content {
    flex-grow: 1;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  
  .title {
    font-weight: bold;
    margin: 0 0 0 10%;
    position: relative;
    z-index: 2;
  }
  
  .icon {
    font-size: 1.5rem;
    margin-right: 0.5rem;
  }
  
  .description {
    color: #666;
    font-size: 0.9rem;
    margin-bottom: 0.75rem;
  }
  
  .progressContainer {
    width: 100%;
    position: relative;
    .lines {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url('../../assets/project/left_bg.svg') no-repeat 0 0, url('../../assets/project/right_bg.svg') no-repeat 100% 0;
      background-size: contain;
    }
    .points {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url('../../assets/project/danger_marker.svg') no-repeat 95% 2%, url('../../assets/project/danger_marker.svg') no-repeat 5% 95%;
    }
  }
  
  .progressBar {
    background-color: #4caf50;
    height: 8px;
    border-radius: 4px;
    transition: width 0.3s ease;
  }
  
  .progressText {
    font-size: 0.8rem;
    color: #666;
    margin-left: auto;
  }
  
  &.completed {
    pointer-events: none;
  }
  
  &.success {
    box-shadow: inset 0 0 10px 0 rgba(76, 175, 80, 0.1);
    
    .progressBar {
      background-color: #4caf50;
    }
  }
  
  &.failed {
    background-color: rgba(244, 67, 54, 0.1);
    border: 1px solid #f44336;
    
    .progressBar {
      background-color: #f44336;
    }
  }
}

// Стили для аварийной кнопки
.emergencyButton {
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  background-color: #ff5252;
  color: white;
  border: none;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 10px rgba(255, 82, 82, 0.4);
  
  &:hover {
    background-color: #e60000;
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(255, 82, 82, 0.6);
  }
  
  &:active {
    transform: scale(0.98);
    box-shadow: 0 1px 5px rgba(255, 82, 82, 0.4);
  }
}

.statusOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
  text-align: center;
}

.statusText {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  animation: pulse 1.5s infinite;
}

.effectText {
  font-size: 1.2rem;
}

.success {
  .statusOverlay {
    background-color: rgba(76, 175, 80, 0.2);
  }
  .statusText {
    color: #4caf50;
  }
  .effectText {
    color: #4caf50;
  }
}

.failed {
  .statusOverlay {
    background-color: rgba(244, 67, 54, 0.2);
  }
  .statusText {
    color: #f44336;
  }
  .effectText {
    color: #f44336;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

// Стили для главного прогресс бара (SVG)
.mainProgressBar {
  margin: 8px 0 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  
  svg {
    width: 100%;
    height: auto;
  }
}

// Стили для малого прогресс бара (символы "|")
.minorProgressBar {
  position: relative;
  height: 20px;
  width: 100%;
  max-width: 449px;
  margin: 0 auto 0;
  overflow: hidden;
}

.progressLayer {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  font-family: monospace;
  font-size: 14px;
  line-height: 1;
  transition: clip-path 1s linear;
  
  &:nth-child(1) {
    color: var(--light-yellow);
  }
  
  &:nth-child(2) {
    color: var(--light-yellow);
  }
  
  &:nth-child(3) {
    color: var(--light-yellow);
  }
}

.progressChar {
  display: inline-block;
  width: 2px;
  text-align: center;
}
.letters {
  display: flex;
  align-items: center;
  z-index: 1;
  position: relative;
  padding-left: 45px;
  overflow: hidden;
  &[data-progress="1"] {
    &::before {
      clip-path: polygon(0 0, 20% 0, 5% 100%, 0 100%);
    }
  }
  &[data-progress="2"] {
    &::before {
      clip-path: polygon(0 0, 30% 0, 15% 100%, 0 100%);
    }
  }
  &[data-progress="3"] {
    &::before {
      clip-path: polygon(0 0, 40% 0, 22% 100%, 0 100%);
    }
  }
  &[data-progress="4"] {
    &::before {
      clip-path: polygon(0 0, 48% 0, 32% 100%, 0 100%);
    }
  }
  &[data-progress="5"] {
    &::before {
      clip-path: polygon(0 0, 55% 0, 42% 100%, 0 100%);
    }
  }
  &[data-progress="6"] {
    &::before {
      clip-path: polygon(0 0, 65% 0, 50% 100%, 0 100%);
    }
  }
  &[data-progress="7"] {
    &::before {
      clip-path: polygon(0 0, 75% 0, 58% 100%, 0 100%);
    }
  }
  &[data-progress="8"] {
    &::before {
      clip-path: polygon(0 0, 85% 0, 68% 100%, 0 100%);
    }
  }
  &[data-progress="9"] {
    &::before {
      clip-path: polygon(0 0, 90% 0, 77% 100%, 0 100%);
    }
  }
  &[data-progress="10"] {
    &::before {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 46px;
    right: 1px;
    height: 100%;
    background: var(--light-yellow);
    z-index: -1;
    clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
  }
}
.mainBarLine {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 449px;
  .icon {
    align-self: flex-end;
    border-radius: 2px;
    border: 1px solid var(--light-yellow);
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 2px;
    &.finished {
      background-color: var(--light-yellow);
    }
  }
}
