.shieldControls {
  display: flex;
  width: 100%;

  .shieldButton {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
    background-color: rgba(24, 24, 36, 0.9);
    color: var(--white-smoke);
    cursor: pointer;
    transition: all 0.2s ease;
    height: 50px;
    max-width: 33.33333333%;
    line-height: 2em;
    
    &:hover {
      background-color: var(--tomato);
    }
    
    &.active {
      background-color: rgba(64, 160, 96, 0.8);
      border-color: rgba(64, 160, 96, 0.8);
      box-shadow: 0 0 10px rgba(64, 160, 96, 0.5);
    }

    &.disabled {
      background-color: rgba(24, 24, 36, 0.9);
      border-color: rgba(24, 24, 36, 0.9);
      box-shadow: none;
      cursor: not-allowed;
      opacity: 0.5;
    }
    
    .icon {
      font-size: 1.5em;
      margin-right: 10px;
      min-width: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    
    .name {
      font-weight: 600;
      font-size: 0.9em;
      white-space: nowrap;
    }
  }
} 