.hackAirlockContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background: rgba(5, 15, 25, 0.95);
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(255, 100, 0, 0.4);
  color: #f0f8ff;
  width: 100vw;
  height: 100vh;
  max-width: none;
  margin: 0;
  font-family: 'Courier New', monospace;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  overflow: auto;
}

.gameHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 800px;
  margin-bottom: 20px;
  position: relative;
  z-index: 10;
  
  h3 {
    margin: 0;
    color: #ff6600;
    font-size: 24px;
    text-shadow: 0 0 10px rgba(255, 100, 0, 0.5);
  }
}

.timer {
  background: rgba(0, 0, 0, 0.4);
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 16px;
  border: 1px solid rgba(255, 100, 0, 0.3);
  
  span {
    font-weight: bold;
    color: #ff9933;
    
    &.timerWarning {
      color: #ff3333;
      animation: pulse 1s infinite;
    }
    
    &.speedWarning {
      color: #ffff33;
      animation: blink 0.5s infinite;
    }
  }
}

.consoleB {
  width: 100%;
  max-width: 800px;
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid rgba(0, 255, 100, 0.6);
  border-radius: 6px;
  margin-bottom: 20px;
  box-shadow: 0 0 15px rgba(0, 255, 100, 0.3);
  position: relative;
  z-index: 10;
}

.screen {
  padding: 15px;
  background: rgba(0, 20, 0, 0.9);
  border-radius: 4px;
  font-family: 'Courier New', monospace;
}

.sequenceDisplay {
  margin-bottom: 10px;
  
  .prompt {
    color: #00ff99;
    font-weight: bold;
  }
  
  .sequence {
    color: #ffffff;
    font-size: 18px;
    letter-spacing: 2px;
    
    .cursor {
      animation: blink 1s infinite;
      color: #00ff99;
    }
  }
}

.statusLine {
  color: #88cc88;
  font-size: 14px;
}

.errorMessage {
  margin-top: 10px;
  padding: 8px 12px;
  background: rgba(255, 50, 50, 0.2);
  border: 1px solid rgba(255, 50, 50, 0.5);
  border-radius: 4px;
  color: #ff6666;
  text-align: center;
  animation: shake 0.5s ease-in-out;
}

.symbolsGrid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 10px;
  width: 100%;
  max-width: 500px;
  margin-bottom: 20px;
  position: relative;
  z-index: 10;
}

.symbolButton {
  background: rgba(30, 30, 60, 0.8);
  border: 2px solid rgba(100, 150, 255, 0.5);
  border-radius: 6px;
  color: #ffffff;
  font-size: 24px;
  padding: 15px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: inherit;
  
  &:hover:not(:disabled) {
    background: rgba(50, 50, 100, 0.9);
    border-color: rgba(150, 200, 255, 0.8);
    box-shadow: 0 0 15px rgba(100, 150, 255, 0.4);
    transform: scale(1.05);
  }
  
  &:active:not(:disabled) {
    transform: scale(0.98);
    background: rgba(70, 70, 120, 0.9);
  }
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.hintsArea {
  width: 100%;
  max-width: 800px;
  margin-top: 10px;
  position: relative;
  z-index: 10;
}


.hintsContainer {
  margin-top: 15px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 150, 0, 0.3);
  border-radius: 6px;
  animation: fadeIn 0.3s ease-in-out;
}

.hintsTitle {
  margin: 0 0 15px 0;
  color: #ffaa33;
  font-size: 16px;
  text-align: center;
}

.environment {
  position: relative;
  width: 100%;
  height: 150px;
  background: linear-gradient(45deg, rgba(20, 20, 30, 0.8), rgba(30, 30, 50, 0.8));
  border-radius: 6px;
  border: 1px solid rgba(100, 100, 150, 0.3);
}

.hint {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  
  .hintSymbol {
    font-size: 20px;
    color: #ffffff;
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.6);
  }
  
  .hintOrder {
    font-size: 12px;
    color: #aaccff;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 4px;
  }
  
  &.floor {
    bottom: 10px;
    left: 20%;
  }
  
  &.ceiling {
    top: 10px;
    right: 25%;
  }
  
  &.leftWall {
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
  
  &.rightWall {
    right: 10px;
    top: 30%;
  }
  
  &.console {
    bottom: 15px;
    right: 20%;
  }
}

.gameOverMessage {
  margin-top: 20px;
  padding: 15px 20px;
  border-radius: 5px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  animation: fadeIn 0.5s ease-in-out;
  position: relative;
  z-index: 10;
  
  &.success {
    background: rgba(0, 150, 50, 0.3);
    color: #00ff99;
    border: 2px solid rgba(0, 200, 100, 0.6);
    box-shadow: 0 0 20px rgba(0, 255, 100, 0.3);
  }
  
  &.failure {
    background: rgba(150, 30, 30, 0.3);
    color: #ff6666;
    border: 2px solid rgba(200, 50, 50, 0.6);
    box-shadow: 0 0 20px rgba(255, 50, 50, 0.3);
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
}

// Стартовый экран
.startScreen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
  
  &.slideUpAnimation {
    animation: slideUp 0.8s ease-out forwards;
  }
}

.startContent {
  text-align: center;
  max-width: 600px;
  padding: 40px;
  background: rgba(20, 40, 60, 0.9);
  border: 2px solid rgba(255, 100, 0, 0.6);
  border-radius: 12px;
  box-shadow: 0 0 30px rgba(255, 100, 0, 0.3);
  
  h2 {
    color: #ff3333;
    font-size: 32px;
    margin-bottom: 30px;
    text-shadow: 0 0 15px rgba(255, 50, 50, 0.8);
    animation: pulse 2s infinite;
  }
}

.storyText {
  margin-bottom: 40px;
  
  p {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 15px;
    color: #f0f8ff;
  }
}

.startButton {
  background: linear-gradient(45deg, rgba(255, 100, 0, 0.8), rgba(255, 150, 50, 0.8));
  border: 2px solid rgba(255, 100, 0, 0.9);
  border-radius: 8px;
  color: #ffffff;
  font-size: 20px;
  font-weight: bold;
  padding: 15px 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  font-family: inherit;
  
  &:hover {
    background: linear-gradient(45deg, rgba(255, 150, 50, 0.9), rgba(255, 200, 100, 0.9));
    box-shadow: 0 0 20px rgba(255, 100, 0, 0.6);
    transform: scale(1.05);
  }
  
  &:active {
    transform: scale(0.98);
  }
}

// Фоновые подсказки
.backgroundHints {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.backgroundHint {
  position: absolute;
  font-size: 60px;
  color: rgba(100, 200, 255, 0.15);
  text-shadow: 0 0 20px rgba(100, 200, 255, 0.3);
  font-weight: bold;
  animation: floating 4s ease-in-out infinite;
  user-select: none;
  pointer-events: none;
}

@keyframes slideUp {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  70% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh);
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes floating {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
    opacity: 0.1;
  }
  50% {
    transform: translateY(-10px) rotate(5deg);
    opacity: 0.2;
  }
} 