.shipManagerContainer {
  position: absolute;
  width: 100%;
  height: 100%;
}

.activeGameOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.activeGameContainer {
  width: 80%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  border-radius: 10px;
  background: rgba(20, 30, 50, 0.9);
  box-shadow: 0 0 30px rgba(0, 150, 255, 0.6);
  padding: 20px;
  position: relative;
}

.closeButton {
  position: absolute;
  top: 10px;
  right: 15px;
  background: none;
  border: none;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
  opacity: 0.7;
  transition: opacity 0.2s;
  
  &:hover {
    opacity: 1;
  }
}

.defaultGameInterface {
  text-align: center;
  padding: 20px;
  
  h3 {
    color: #00ccff;
    margin-bottom: 30px;
    font-size: 24px;
  }
  
  p {
    color: #aaccff;
    margin-bottom: 20px;
    font-size: 16px;
  }
  
  button {
    background: rgba(0, 150, 255, 0.8);
    border: none;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.2s;
    
    &:hover {
      background: rgba(0, 180, 255, 1);
    }
  }
}

.gameInProgress {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  color: #e0f0ff;
  font-size: 18px;
}

.loadingSpinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(0, 200, 255, 0.3);
  border-top: 4px solid #00ccff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.gameResult {
  padding: 20px;
  border-radius: 8px;
  margin-top: 20px;
  font-size: 22px;
  font-weight: bold;
  
  &.success {
    background: rgba(0, 150, 50, 0.3);
    color: #00ff99;
    border: 2px solid rgba(0, 200, 100, 0.5);
  }
  
  &.failure {
    background: rgba(150, 30, 30, 0.3);
    color: #ff5555;
    border: 2px solid rgba(200, 50, 50, 0.5);
  }
}

.effectInfo {
  margin-top: 20px;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.effectType {
  color: #aaccff;
}

.effectValue {
  font-weight: bold;
  font-size: 18px;
  
  .success & {
    color: #00ff99;
  }
  
  .failure & {
    color: #ff5555;
  }
}

.effectDuration {
  color: #aaccff;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.gameResult {
  font-size: 1.6rem;
  font-weight: bold;
  padding: 15px;
  margin: 10px 0;
  border-radius: 8px;
  animation: resultPulse 1.5s infinite;
  
  &.success {
    color: #4caf50;
    background-color: rgba(76, 175, 80, 0.1);
    border: 1px solid rgba(76, 175, 80, 0.3);
  }
  
  &.failure {
    color: #f44336;
    background-color: rgba(244, 67, 54, 0.1);
    border: 1px solid rgba(244, 67, 54, 0.3);
  }
}

.effectInfo {
  font-size: 1rem;
  margin-top: 15px;
  padding: 10px;
  border-radius: 6px;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 5px;
  animation: none;
  color: var(--white-smoke);
}

.effectType {
  font-size: 0.9rem;
  opacity: 0.9;
}

.effectValue {
  font-size: 1.2rem;
  font-weight: bold;
  margin: 5px 0;
}

.effectDuration {
  font-size: 0.8rem;
  opacity: 0.7;
}

@keyframes resultPulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.9;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
} 