.devTools {
  position: fixed;
  top: 0;
  right: 20%;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.toggleButton {
  padding: 8px 12px;
  border-radius: 50%;
  border: 1px solid #ccc;
  background-color: #f0f0f0;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:hover {
    background-color: #e0e0e0;
  }
  
  &.active {
    background-color: #4CAF50;
    color: white;
    
    &:hover {
      background-color: #45a049;
    }
  }
}

.devToolsContent {
  display: flex;
  gap: 10px;
  margin-top: 10px;
  flex-wrap: wrap;
  max-width: 300px;
}

.devToolsContent button {
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: #f0f0f0;
  cursor: pointer;
  font-size: 12px;
  
  &:hover {
    background-color: #e0e0e0;
  }
  
  &.active {
    background-color: #4CAF50;
    color: white;
    
    &:hover {
      background-color: #45a049;
    }
  }
}

.scenarioSection {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 6px;
  background-color: #f9f9f9;
  width: 100%;
}

.sectionTitle {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}

.scenarioButton {
  padding: 10px 15px !important;
  border-radius: 6px !important;
  border: 2px solid #4CAF50 !important;
  background-color: #fff !important;
  color: #4CAF50 !important;
  cursor: pointer;
  font-size: 13px !important;
  font-weight: 600;
  transition: all 0.2s ease;
  
  &:hover {
    background-color: #4CAF50 !important;
    color: white !important;
    transform: translateY(-1px);
    box-shadow: 0 2px 4px rgba(76, 175, 80, 0.3);
  }
}

.analyticsContainer {
  position: fixed;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
  width: 90%;
  max-width: 1200px;
  max-height: 80vh;
  overflow-y: auto;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  background-color: white;
}
