.step1 {
  display: grid;
  grid-template-areas:
  "h h"
  "d m"
  "s m";
  transition: all var(--wireframe-transition);
  grid-template-rows: 40px 1px calc(100vh - 40px);
  grid-template-columns: 1px 1fr;
  height: 100vh;
  &.visible {
    opacity: 1;
  }
  &.sidebar {
    grid-template-columns: 0.3fr 1fr;
  }
  &.talent {
    grid-template-rows: 40px 50vh calc(50vh - 40px);
    grid-template-columns: 0.4fr 1fr;
  }
}



.mainHeader {
  grid-area: h;
  background: var(--alabaster);
  display: block;
  position: relative;
  text-align: left;
  font-weight: bold;
  transition: background-color var(--wireframe-transition), box-shadow var(--wireframe-transition), border var(--wireframe-transition), border-image var(--wireframe-transition), filter var(--wireframe-transition);
}
.header {
  font-size: 1.4em;
  color: var(--white-smoke);
  text-shadow: 1px 1px 2px var(--cornell-red);
  position: relative;
  &::after {
    content: "";
    position: absolute;
    display: inline-block;
    top: 0px;
    right: 0;
    font-size: 1.4em;
    text-shadow: none;
    background: url(../../assets/stripe.png) 0 100%;
    width: 35%;
    height: 24px;
    filter: invert(1);
    opacity: 1;
  }
}

.helpButton {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background-color: var(--cinnabar);
  color: white;
  border: none;
  font-size: 16px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.2s, background-color 0.2s, background-color var(--wireframe-transition), box-shadow var(--wireframe-transition), border var(--wireframe-transition), border-image var(--wireframe-transition), filter var(--wireframe-transition);
  
  &:hover {
    transform: translateY(-50%) scale(1.1);
    background-color: darken(#e8514c, 10%);
  }
  
  &:active {
    transform: translateY(-50%) scale(0.95);
  }
}

.tooltipContainer {
  position: relative;
  display: inline-block;
  margin-left: 5px;
  z-index: 10;
}

.infoIcon {
  cursor: pointer;
  color: var(--cinnabar);
  font-size: 14px;
}

.tooltip {
  visibility: hidden;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 8px 12px;
  border-radius: 4px;
  width: 250px;
  font-size: 12px;
  line-height: 1.4;
  z-index: 100;
  top: calc(100% + 5px);
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
  text-align: left;
  
  &::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color:  transparent  transparent rgba(0, 0, 0, 0.8) transparent;
  }
}

.tooltipContainer:hover .tooltip {
  visibility: visible;
  opacity: 1;
}

.development {
  grid-area: d;
  background: var(--melon);
  box-shadow: inset 0px 0px 3px 9px var(--alabaster), inset 0px 0px 0px 10px var(--cinnabar);
  padding: 10px;
  overflow: hidden;
  transition: background-color var(--wireframe-transition), box-shadow var(--wireframe-transition), border var(--wireframe-transition), border-image var(--wireframe-transition), filter var(--wireframe-transition);
  margin-bottom: -1px;
}

.click {
  grid-area: l;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.mapContainer {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 200px;
  grid-area: m;
  overflow-y: scroll;
  border-radius: 10px;
  background: white;
  transition: background-color var(--wireframe-transition), box-shadow var(--wireframe-transition), border var(--wireframe-transition), border-image var(--wireframe-transition), filter var(--wireframe-transition);
  
  :global(.map) {
    transition: transform 0.1s ease;
    
    &.clicking {
      transform: scale(0.95) !important;
    }
  }
  &.cosmosContainer {
    overflow: hidden;
    border-radius: 0;
  }
}
.projects, .upgrades, .development{
  position: relative;
  &::before{
    content: "";
    position: absolute;
    top: 0.1%;
    left: 0.1%;
    width: 99.8%;
    height: 99.8%;
    z-index: -1;
    opacity: 0;
    background-color: white;
    border: 40px solid transparent;
    border-image: url('../../assets/Selected_=true.png') 105 105 105 105;
    transition: opacity var(--wireframe-transition);
  }
}

.projects {
  display: none;
  grid-area: p;
  background: var(--melon);
  box-shadow: inset 0px 0px 3px 9px var(--alabaster), inset 0px 0px 0px 10px var(--cinnabar);
  padding: 10px;
  max-height: 35vh;
  overflow: hidden;
  transition: background-color var(--wireframe-transition), box-shadow var(--wireframe-transition), border var(--wireframe-transition), border-image var(--wireframe-transition), filter var(--wireframe-transition);
}

.upgrades {
  grid-area: s;
  background: var(--melon);
  box-shadow: inset 0px 0px 3px 9px var(--alabaster), inset 0px 0px 0px 10px var(--cinnabar);
  display: flex;
  padding: 10px;
  flex-direction: column;
  transition: background-color var(--wireframe-transition), box-shadow var(--wireframe-transition), border var(--wireframe-transition), border-image var(--wireframe-transition), filter var(--wireframe-transition);
}

.shop {
  max-height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 100%;
  
  button {
    &:disabled {
      opacity: 50%;
    }
    color: white;
    border: 0;
    min-height: 50px;
    transition: background-color var(--wireframe-transition), box-shadow var(--wireframe-transition), border var(--wireframe-transition), border-image var(--wireframe-transition), filter var(--wireframe-transition);
  }
}

.particlesCanvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.coinsContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 2;
}

.wireframe {
  position: relative;
  background-color: rgba(0, 0, 0, 1);
  box-shadow: none;
  transition: background-color var(--wireframe-transition), box-shadow var(--wireframe-transition), border var(--wireframe-transition), border-image var(--wireframe-transition), filter var(--wireframe-transition);
  &::before{
    opacity: 1;
  };
  .header{
    &::after{
      opacity: 0;
    }
    .infoIcon{
      opacity: 0;
      width: 0;
      height: 0;
    }
  }
}

