.shipContainer {
  position: absolute;
  width: 100px;
  height: 100px;
  z-index: 100;
  transition: all 0.5s ease;
}

.ship {
  font-size: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

/* Стили для отделов корабля */
.shipSection {
  position: absolute;
  width: 25px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.6);
  border: 1px solid #333;
  border-radius: 3px;
  cursor: pointer;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease;
  box-shadow: 0 0 3px rgba(255, 255, 255, 0.3);
  z-index: 2;
  
  &:hover {
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
  }
  
  &.available {
    background-color: rgba(100, 255, 100, 0.8);
    box-shadow: 0 0 8px rgba(100, 255, 100, 0.8);
    animation: pulse 2s infinite;
    cursor: pointer;
    
    &:hover {
      background-color: rgba(120, 255, 120, 0.9);
      box-shadow: 0 0 12px rgba(120, 255, 120, 0.9);
    }
  }
  
  &.cooldown {
    cursor: not-allowed;
    background-color: rgba(150, 150, 150, 0.6);
    
    &:hover {
      background-color: rgba(150, 150, 150, 0.6);
      box-shadow: 0 0 3px rgba(255, 255, 255, 0.3);
    }
  }
  
  &.locked {
    cursor: not-allowed;
    background-color: rgba(100, 100, 100, 0.4);
    
    &:hover {
      background-color: rgba(100, 100, 100, 0.4);
      box-shadow: 0 0 3px rgba(255, 255, 255, 0.2);
    }
  }
}

/* Индикатор готовности */
.readyIndicator {
  position: absolute;
  top: -5px;
  right: -5px;
  width: 8px;
  height: 8px;
  background-color: #00ff00;
  border-radius: 50%;
  font-size: 6px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  animation: blink 1s infinite;
}

/* Стили для отдельных секций */
.bridge {
  background-color: rgba(255, 215, 0, 0.6);
  
  &:hover {
    background-color: rgba(255, 215, 0, 0.8);
  }
  
  &.available {
    background-color: rgba(255, 255, 0, 0.8);
    box-shadow: 0 0 8px rgba(255, 255, 0, 0.7);
  }
}

.engine {
  background-color: rgba(255, 100, 0, 0.6);
  
  &:hover {
    background-color: rgba(255, 100, 0, 0.8);
  }
  
  &.available {
    background-color: rgba(255, 150, 50, 0.8);
    box-shadow: 0 0 8px rgba(255, 120, 0, 0.7);
  }
}

.research {
  background-color: rgba(0, 150, 255, 0.6);
  
  &:hover {
    background-color: rgba(0, 150, 255, 0.8);
  }
  
  &.available {
    background-color: rgba(50, 200, 255, 0.8);
    box-shadow: 0 0 8px rgba(0, 200, 255, 0.7);
  }
}

.defense {
  background-color: rgba(200, 0, 200, 0.6);
  
  &:hover {
    background-color: rgba(200, 0, 200, 0.8);
  }
  
  &.available {
    background-color: rgba(255, 50, 255, 0.8);
    box-shadow: 0 0 8px rgba(255, 0, 255, 0.7);
  }
}

/* Таймер кулдауна */
.cooldownTimer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 8px;
  font-weight: bold;
  color: white;
  text-shadow: 0 0 2px black;
}

/* Анимации */
@keyframes pulse {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.1);
    opacity: 0.8;
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
}

@keyframes blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
} 