.questSystem {
  position: fixed;
  right: 20px;
  top: 60%;
  transform: translateY(-50%);
  width: 300px;
  max-height: 70vh;
  z-index: 1;

  
  transition: all 0.3s ease;
  
  &.collapsed {
    width: 0px;
    height: 0px;
    border-radius: 50%;
    
    .questHeader {
      justify-content: center;
      padding: 15px;
    }
  }
}

.questsHeader {  
  cursor: pointer;
  position: absolute;
  top: -42px;
  right: 10px;
  background: rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(2px);
  padding: 5px;
  .questIcon {
    font-size: 24px;
    transition: transform 0.2s ease;
    
    .collapsed & {
      font-size: 11px;
    }
  }
}





.questList {
  max-height: calc(70vh - 60px);
  overflow-y: hidden;
  
  // Кастомный скроллбар
  &::-webkit-scrollbar {
    width: 6px;
  }
  
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.3);
    border-radius: 3px;
    
    &:hover {
      background: rgba(255, 255, 255, 0.5);
    }
  }
}

.questItem {
  text-align: left;
  
  padding: 10px;
  transition: all 0.3s ease;
  // Эффект замерзшего стекла
  background: rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(2px);
  border-radius: 2px;
  
  &:hover {
    background: rgba(255, 255, 255, 1);
    border-color: rgba(255, 255, 255, 0.2);
  }
  
  &.completed {
    background: rgba(0, 255, 0, 0.1);
    border-color: rgba(0, 255, 0, 0.3);
    
    .questTitle {
      color: var(--light-yellow);
    }
    
    .completedIcon {
      color: #00ff00;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 12px;
      font-weight: bold;
      margin-left: 8px;
      animation: completePulse 0.5s ease;
    }
  }
  
  &.completing {
    animation: slideOutRight 1s ease-in-out forwards;
  }
}

.questHeader {
  margin-bottom: 8px;
  
  .questTitle {
    color: var(--gold2);
    font-weight: bold;
    text-shadow: 0 0 1px var(--light-yellow);
  }
}
.questContent {
  color: var(--gunmetal);
  text-shadow: 0 0 6px white;
}

.questDescription {
  font-size: 12px;
  line-height: 1.4;
  margin-bottom: 10px;
  display: inline;
}


.progressText {
  font-size: 1.2em;
  font-weight: bold;
  min-width: 40px;
  text-align: right;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

// Анимации
@keyframes completePulse {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes slideOutRight {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(100%);
    opacity: 0;
  }
}

// Адаптивность для мобильных устройств
@media (max-width: 768px) {
  .questSystem {
    right: 10px;
    width: 250px;
    max-height: 60vh;
    
    &.collapsed {
      width: 50px;
      height: 50px;
    }
  }
  
  .questHeader {
    padding: 12px 15px;
  }
  
  .questIcon {
    font-size: 20px;
  }
  
  .questTitle {
    font-size: 14px;
  }
} 