.urgentMessage {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  background: linear-gradient(135deg, #ff4444, #cc0000);
  border: 2px solid #fff;
  border-radius: 0 0 10px 10px;
  box-shadow: 0 4px 20px rgba(255, 68, 68, 0.6);
  animation: slideDown 0.5s ease-out, urgentPulse 2s infinite ease-in-out;
  min-width: 400px;
  max-width: 600px;
  padding: 0;
}

.messageContent {
  padding: 15px 25px;
  text-align: center;
  color: white;
  font-weight: bold;
}

.title {
  font-size: 16px;
  margin-bottom: 8px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
  letter-spacing: 1px;
}

.timer {
  font-size: 28px;
  font-family: 'Courier New', monospace;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 6px;
  padding: 8px 16px;
  margin: 8px auto;
  display: inline-block;
  cursor: pointer;
  transition: all 0.2s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
  min-width: 120px;
  
  &:hover {
    background: rgba(0, 0, 0, 0.5);
    border-color: rgba(255, 255, 255, 0.6);
    transform: scale(1.05);
  }
  
  &:active {
    transform: scale(0.98);
  }
}

.subtitle {
  font-size: 12px;
  margin-top: 8px;
  opacity: 0.9;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  line-height: 1.3;
  max-width: 350px;
  margin-left: auto;
  margin-right: auto;
}

@keyframes slideDown {
  from {
    transform: translateX(-50%) translateY(-100%);
  }
  to {
    transform: translateX(-50%) translateY(0);
  }
}

@keyframes urgentPulse {
  0%, 100% {
    box-shadow: 0 4px 20px rgba(255, 68, 68, 0.6);
  }
  50% {
    box-shadow: 0 4px 30px rgba(255, 68, 68, 0.9), 0 0 15px rgba(255, 68, 68, 0.5);
  }
}

/* Адаптивный дизайн */
@media (max-width: 600px) {
  .urgentMessage {
    min-width: 90%;
    max-width: 90%;
  }
  
  .timer {
    font-size: 24px;
  }
  
  .title {
    font-size: 14px;
  }
  
  .subtitle {
    font-size: 11px;
  }
} 