.resourcesHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 10px;
  background-color: var(--cinnabar);
  height: 100%;
  user-select: none;
  width: 100%;
  transition: background-color 3s ease, box-shadow 3s ease;
}

.mainResources {
  display: flex;
  align-items: center;
  padding: 4px 12px;
  border-left: 1px solid var(--cinnabar);
  margin-left: 15px;
  min-width: 150px;
  background-color: var(--eerie-black);
  border-radius: 3px;
  cursor: help;
  transition: all 0.2s ease, background-color 3s ease, border 3s ease, box-shadow 3s ease;
  box-shadow:
    inset -1px -2px 0 2px rgba(255,255,255,0.2),
    inset 0 0 0 1px var(--cinnabar);
}

.talentPoints {
  display: flex;
  align-items: center;
  padding: 4px 12px;
  border-left: 1px solid var(--cinnabar);
  margin-left: 15px;
  min-width: 100px;
  background-color: var(--eerie-black);
  border-radius: 3px;
  cursor: help;
  transition: all 0.2s ease, background-color 3s ease, border 3s ease, box-shadow 3s ease;
  box-shadow:
    inset -1px -2px 0 2px rgba(255,255,255,0.2),
    inset 0 0 0 1px var(--cinnabar);
}

.talentValue {
  display: flex;
  align-items: center;
  font-size: 1.1rem;
  font-weight: bold;
  color: var(--white-smoke);
}

.available {
  color: var(--gold);
  font-weight: bold;
}

.separator {
  margin: 0 4px;
  color: var(--white-smoke);
  opacity: 0.7;
}

.total {
  color: var(--white-smoke);
  opacity: 0.8;
}

.icon {
  margin-right: 10px;
  font-size: 1.2rem;
  transition: filter 3s ease;
}

.value {
  font-size: 1.1rem;
  font-weight: bold;
  color: var(--white-smoke);
}

.countryInfo {
  flex: 1;
  text-align: center;
  color: var(--alabaster);
  font-weight: bold;
  font-size: 1.1rem;
  padding: 4px 15px;
  cursor: help;
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 3px;
  border: 1px solid var(--cinnabar);
  background-color: var(--eerie-black);
  transition: all 0.2s ease, background-color 3s ease, border 3s ease, box-shadow 3s ease;
}

.dateDisplay {
  display: flex;
  justify-content: center;
  align-items: center;
}

.monthPart {
  display: inline-block;
  width: 85px; /* Фиксированная ширина для самого длинного месяца */
  text-align: right;
  margin-right: 5px;
}

.yearPart {
  display: inline-block;
  text-align: left;
}

.spheresContainer {
  display: flex;
  flex-grow: 1;
  justify-content: flex-start;
  max-width: 33%;
}

.sphereResource {
  display: flex;
  align-items: center;
  padding: 4px 10px;
  min-width: 33%;
  background-color: var(--eerie-black);
  border-radius: 3px;
  cursor: help;
  transition: all 0.2s ease, background-color 3s ease, border 3s ease, box-shadow 3s ease;
  box-shadow:
    inset -1px -2px 0px 2px rgba(255,255,255,0.2),
    inset 0 0 0 1px var(--cinnabar);
}

.sphereIcon {
  margin-right: 8px;
  font-size: 1.2rem;
  transition: filter 3s ease;
}

.sphereValue {
  font-size: 1rem;
  font-weight: bold;
  color: var(--white-smoke);
}

.counter {
  white-space: nowrap;
}

.tooltip {
  position: fixed;
  background-color: var(--gunmetal);
  color: var(--alabaster);
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 0.9rem;
  max-width: 250px;
  z-index: 1000;
  border: 1px solid var(--cinnabar);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.5);
  pointer-events: none;
  
  &::before {
    content: '';
    position: absolute;
    top: -6px;
    left: 20px;
    width: 0;
    height: 0;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid var(--gunmetal);
  }
}
.wireframe {
  filter: grayscale(100%) contrast(0.8);
  box-shadow: none;
  transition: background-color 3s ease, box-shadow 3s ease;
  transition: filter 3s ease;
  .sphereResource, .mainResources, .countryInfo, .talentPoints{
    background-color: transparent;
    border: none;
    box-shadow: none;
    position: relative;
    transition: background-color 3s ease, border 3s ease, box-shadow 3s ease, border-image 3s ease;
    .sphereIcon, .icon{
      filter: grayscale(100%) contrast(0.8);
      transition: filter 3s ease;
    }
    &::before{
      content: "";
      position: absolute;
      top: 0%;
      left: 0%;
      width: 100%;
      height: 100%;
      border: 10px solid transparent;
      border-image: url('../../assets/thin_border.png') 20 20 20 20;
    }
  }
}

