.popupBackdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--alabaster);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popupContent {
  background: var(--alabaster);
  padding: 20px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popupTitle {
  color: var(--cinnabar);
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
}

.gameStepsContainer {
  width: 100%;
  overflow-x: auto;
  margin-bottom: 30px;
  padding: 10px 0;
  
  &::-webkit-scrollbar {
    height: 8px;
  }
  
  &::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.05);
    border-radius: 4px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: var(--cinnabar);
    border-radius: 4px;
  }
}

.gameSteps {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px;
  min-width: max-content;
}

.step {
  background-color: var(--white-smoke);
  padding: 10px 15px;
  border-radius: 3px;
  margin: 0 5px;
  font-weight: bold;
  color: var(--cinnabar);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  font-size: 14px;
  max-width: 10em;
  
  img {
    max-width: 100%;
  }
}

.arrow {
  font-size: 24px;
  margin: 0 5px;
  color: var(--cinnabar);
}

.okButton {
  padding: 10px 50px;
  background-color: var(--cinnabar);
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  margin-top: 10px;
  transition: transform 0.2s, background-color 0.2s;
  
  &:hover {
    background-color: darken(#e8514c, 10%);
    transform: scale(1.05);
  }
  
  &:active {
    transform: scale(0.95);
  }
} 