.exoticSynthesisContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background: rgba(10, 5, 25, 0.95);
  border-radius: 8px;
  box-shadow: 0 0 25px rgba(150, 50, 255, 0.4);
  color: #f0f8ff;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  font-family: 'Courier New', monospace;
  min-height: 600px;
}

.gameHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
  
  h3 {
    margin: 0;
    color: #9966ff;
    font-size: 24px;
    text-shadow: 0 0 10px rgba(150, 100, 255, 0.5);
  }
}

.timer {
  background: rgba(0, 0, 0, 0.4);
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 16px;
  border: 1px solid rgba(150, 100, 255, 0.3);
  
  span {
    font-weight: bold;
    color: #bb88ff;
    
    &.timerWarning {
      color: #ff3333;
      animation: pulse 1s infinite;
    }
  }
}

.targetInfo {
  text-align: center;
  margin-bottom: 20px;
  background: rgba(0, 0, 0, 0.3);
  padding: 15px;
  border-radius: 6px;
  border: 1px solid rgba(150, 100, 255, 0.3);
  
  h4 {
    margin: 0 0 10px 0;
    color: #ccaaff;
    font-size: 18px;
  }
}

.targetComposition {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  
  span {
    color: #aaccff;
    font-size: 14px;
  }
}

.reactorContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}

.reactor {
  position: relative;
  width: 300px;
  height: 400px;
  background: linear-gradient(180deg, 
    rgba(100, 150, 255, 0.1) 0%,
    rgba(150, 100, 255, 0.2) 50%,
    rgba(100, 150, 255, 0.1) 100%);
  border: 3px solid rgba(150, 100, 255, 0.6);
  border-radius: 50px 50px 20px 20px;
  box-shadow: 
    0 0 20px rgba(150, 100, 255, 0.3),
    inset 0 0 30px rgba(100, 150, 255, 0.1);
  overflow: hidden;
  
  &.exploding {
    animation: explosion 1s ease-out;
    box-shadow: 
      0 0 50px rgba(255, 100, 100, 0.8),
      inset 0 0 30px rgba(255, 50, 50, 0.3);
  }
  
  &.unstable {
    animation: instabilityFlicker 0.5s infinite;
    border-color: rgba(255, 200, 0, 0.8);
    box-shadow: 
      0 0 30px rgba(255, 200, 0, 0.6),
      inset 0 0 30px rgba(255, 150, 0, 0.2);
  }
}

.magneticField {
  position: absolute;
  top: 10%;
  left: 10%;
  right: 10%;
  bottom: 10%;
  background: radial-gradient(ellipse at center,
    rgba(100, 200, 255, 0.05) 0%,
    rgba(150, 100, 255, 0.1) 50%,
    transparent 70%);
  border-radius: 50%;
  animation: magneticPulse 3s ease-in-out infinite;
}

.particle {
  position: absolute;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 16px;
  transition: all 0.1s ease;
  box-shadow: 0 0 10px currentColor;
  
  &.proton {
    background: rgba(255, 68, 68, 0.8);
    color: #ffffff;
    border: 2px solid #ff4444;
  }
  
  &.neutron {
    background: rgba(136, 136, 136, 0.8);
    color: #ffffff;
    border: 2px solid #888888;
  }
  
  &.electron {
    background: rgba(68, 68, 255, 0.8);
    color: #ffffff;
    border: 2px solid #4444ff;
  }
}

.currentCounts {
  margin-top: 15px;
  display: flex;
  gap: 20px;
  background: rgba(0, 0, 0, 0.3);
  padding: 10px 15px;
  border-radius: 4px;
  border: 1px solid rgba(100, 100, 150, 0.3);
  
  div {
    color: #aaccff;
    font-size: 14px;
  }
}

.controls {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.particleButton {
  background: rgba(30, 30, 60, 0.8);
  border: 2px solid rgba(100, 150, 255, 0.5);
  border-radius: 8px;
  color: #ffffff;
  padding: 15px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: inherit;
  text-align: center;
  min-width: 120px;
  
  &:hover:not(:disabled) {
    background: rgba(50, 50, 100, 0.9);
    border-color: rgba(150, 200, 255, 0.8);
    box-shadow: 0 0 15px rgba(100, 150, 255, 0.4);
    transform: scale(1.05);
  }
  
  &:active:not(:disabled) {
    transform: scale(0.98);
  }
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  &.proton {
    border-color: rgba(255, 68, 68, 0.6);
    
    &:hover:not(:disabled) {
      border-color: rgba(255, 100, 100, 0.8);
      box-shadow: 0 0 15px rgba(255, 68, 68, 0.4);
    }
  }
  
  &.neutron {
    border-color: rgba(136, 136, 136, 0.6);
    
    &:hover:not(:disabled) {
      border-color: rgba(170, 170, 170, 0.8);
      box-shadow: 0 0 15px rgba(136, 136, 136, 0.4);
    }
  }
  
  &.electron {
    border-color: rgba(68, 68, 255, 0.6);
    
    &:hover:not(:disabled) {
      border-color: rgba(100, 100, 255, 0.8);
      box-shadow: 0 0 15px rgba(68, 68, 255, 0.4);
    }
  }
}

.buttonSymbol {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 5px;
}

.buttonName {
  font-size: 14px;
  margin-bottom: 8px;
  color: #cccccc;
}

.buttonStats {
  font-size: 11px;
  color: #aaaaaa;
  line-height: 1.2;
}

.stabilityMessage {
  margin-bottom: 15px;
  padding: 10px 15px;
  border-radius: 4px;
  text-align: center;
  font-weight: bold;
  animation: fadeIn 0.3s ease-in-out;
  
  &.success {
    background: rgba(0, 150, 50, 0.3);
    color: #00ff99;
    border: 1px solid rgba(0, 200, 100, 0.6);
  }
  
  &.error {
    background: rgba(150, 30, 30, 0.3);
    color: #ff6666;
    border: 1px solid rgba(200, 50, 50, 0.6);
    animation: shake 0.5s ease-in-out;
  }
  
  &:not(.success):not(.error) {
    background: rgba(100, 100, 150, 0.2);
    color: #ccccff;
    border: 1px solid rgba(100, 100, 150, 0.4);
  }
}

.gameOverMessage {
  margin-top: 20px;
  padding: 15px 20px;
  border-radius: 5px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  animation: fadeIn 0.5s ease-in-out;
  
  &.success {
    background: rgba(0, 150, 50, 0.3);
    color: #00ff99;
    border: 2px solid rgba(0, 200, 100, 0.6);
    box-shadow: 0 0 20px rgba(0, 255, 100, 0.3);
  }
  
  &.failure {
    background: rgba(150, 30, 30, 0.3);
    color: #ff6666;
    border: 2px solid rgba(200, 50, 50, 0.6);
    box-shadow: 0 0 20px rgba(255, 50, 50, 0.3);
  }
}

.instabilityWarning {
  position: absolute;
  top: -60px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 150, 0, 0.9);
  color: #000000;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  text-align: center;
  animation: warningPulse 0.5s infinite;
  box-shadow: 0 0 20px rgba(255, 150, 0, 0.8);
  z-index: 100;
  
  .countdown {
    margin-top: 5px;
    font-size: 14px;
    color: #cc0000;
    font-weight: bold;
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes magneticPulse {
  0%, 100% {
    transform: scale(1);
    opacity: 0.3;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.6;
  }
}

@keyframes explosion {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
    background: rgba(255, 100, 100, 0.4);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
}

@keyframes instabilityFlicker {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.6;
  }
}

@keyframes warningPulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
} 