.coinsContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 9999;

  // Стили для HTML элементов во всплывающих эффектах
  span {
    display: inline;
    
    &.positive {
      color: #4CAF50;
      font-weight: bold;
    }
    
    &.negative {
      color: #F44336;
      font-weight: bold;
    }
    
    &.neutral {
      color: #FFD700;
      font-weight: bold;
    }
    
    &.project {
      color: #2196F3;
      font-weight: bold;
    }
    
    &.faith-req {
      color: #9C27B0;
      font-weight: bold;
    }
    
    &.science-req {
      color: #00BCD4;
      font-weight: bold;
    }
    
    &.economy-req {
      color: #FF9800;
      font-weight: bold;
    }
  }
} 