.cosmos {
    grid-area: m;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    background-color: #000;
    color: var(--light-yellow);
    
    &.shakeLight {
        .background {
            animation: shakeLight 0.6s infinite linear;
        }
    }
    
    &.shakeMedium {
        .background {
            animation: shakeMedium 0.5s infinite linear;
        }
    }
    
    &.shakeHeavy {
        .background {
            animation: shakeHeavy 0.4s infinite linear;
        }
    }
    
    &.shakeIntense {
        .background {
            animation: shakeIntense 0.3s infinite linear;
        }
    }
}

@keyframes shakeLight {
    0% { transform: translate(0); }
    10% { transform: translate(-1px, -1px); }
    20% { transform: translate(1px, 0px); }
    30% { transform: translate(-1px, 1px); }
    40% { transform: translate(0px, -1px); }
    50% { transform: translate(-1px, 0px); }
    60% { transform: translate(1px, -1px); }
    70% { transform: translate(-1px, -1px); }
    80% { transform: translate(1px, 1px); }
    90% { transform: translate(-1px, 0px); }
    100% { transform: translate(0); }
}

@keyframes shakeMedium {
    0% { transform: translate(0); }
    10% { transform: translate(-2px, -1px); }
    20% { transform: translate(2px, 1px); }
    30% { transform: translate(-1px, 2px); }
    40% { transform: translate(1px, -2px); }
    50% { transform: translate(-2px, 1px); }
    60% { transform: translate(2px, -1px); }
    70% { transform: translate(-1px, -2px); }
    80% { transform: translate(2px, 2px); }
    90% { transform: translate(-2px, -1px); }
    100% { transform: translate(0); }
}

@keyframes shakeHeavy {
    0% { transform: translate(0); }
    10% { transform: translate(-3px, -2px); }
    20% { transform: translate(3px, 2px); }
    30% { transform: translate(-2px, 3px); }
    40% { transform: translate(2px, -3px); }
    50% { transform: translate(-3px, 2px); }
    60% { transform: translate(3px, -2px); }
    70% { transform: translate(-2px, -3px); }
    80% { transform: translate(3px, 3px); }
    90% { transform: translate(-3px, -2px); }
    100% { transform: translate(0); }
}

@keyframes shakeIntense {
    0% { transform: translate(0); }
    5% { transform: translate(-4px, -3px); }
    10% { transform: translate(4px, 2px); }
    15% { transform: translate(-3px, 4px); }
    20% { transform: translate(3px, -4px); }
    25% { transform: translate(-4px, 3px); }
    30% { transform: translate(4px, -2px); }
    35% { transform: translate(-2px, -4px); }
    40% { transform: translate(4px, 4px); }
    45% { transform: translate(-4px, -3px); }
    50% { transform: translate(3px, 2px); }
    55% { transform: translate(-2px, 4px); }
    60% { transform: translate(4px, -3px); }
    65% { transform: translate(-3px, 2px); }
    70% { transform: translate(2px, -4px); }
    75% { transform: translate(-4px, -2px); }
    80% { transform: translate(4px, 3px); }
    85% { transform: translate(-3px, -4px); }
    90% { transform: translate(4px, 2px); }
    95% { transform: translate(-4px, -3px); }
    100% { transform: translate(0); }
}

@keyframes starsMoving {
    0% { transform: translateX(0); }
    50% { transform: translateX(-100%); }
    100% { transform: translateX(0); }
}

.background {
    width: 900%;
    height: 100%;    
    position: absolute;
    user-select: none;
    background-image: url('../../../assets/PIA03239.jpg');
    background-size: auto 100%;
    background-position: left center;
    will-change: transform;
    animation: starsMoving 11000s linear infinite;
}

.planets {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1vw;
    justify-items: center;
    height: 100%;

}

.group {
    position: relative;
    &.visible {
        .planet {
            visibility: visible;
        }
    }
    &.locked {
        .planet {
            visibility: visible;
            background: black;
            opacity: 0.8;
            filter: grayscale(1) brightness(0.2) blur(3px);
        }
    }
}

.planet {
    width: 8vw;
    height: 8vw;
    border-radius: 50%;
    background-color: #000;
    position: relative;
    visibility: hidden;
    &.visible {
        visibility: visible;
    }
    &.locked {
        visibility: visible;
        background: black;
        opacity: 0.8;
        filter: grayscale(1) brightness(0.2) blur(3px);
    }
}
.earth {
    top: 75%;
    width: 10vw;
    height: 10vw;
    cursor: pointer;
    transition: transform 0.3s ease;
    background: url('../../../assets/PIA23774-Comparison-Earth-Keper1649c-20200415.jpg') no-repeat left center / cover;
    visibility: visible;
    &.bounce {
        animation: bounce 0.5s ease;
    }
}

@keyframes bounce {
    0% { transform: scale(1); }
    40% { transform: scale(1.02); }
    60% { transform: scale(1.01); }
    80% { transform: scale(1); }
    90% { transform: scale(1.005); }
    100% { transform: scale(1); }
}

